<script>
let ROOT_PATH = 'https://www.jlouie.me'
import sidebar from '@/components/sidebar'
import bodyContent from '@/components/content'
export default {
  components: {
    sidebar,
    bodyContent
  },
  data() {
    return {
      logo: ROOT_PATH + require('./assets/me.jpg'),
      desc: "I'm a Front End UI Designer who's knowledgeable and experienced in working with UX Designers and an enthusiast photographer"
    }
  },
  metaInfo() {
    return {
      title: 'John Louie Binas',
      titleTemplate: '%s | Building Awesoness',
      meta: [
        // Twitter Card
        {name: 'twitter:card', content: 'summary'},
        {name: 'twitter:title', content: 'John Louie Binas'},
        {name: 'twitter:description', content: this.desc},
        // image must be an absolute path
        {name: 'twitter:image', content: this.logo},
        // Facebook OpenGraph
        {property: 'og:title', content: 'John Louie Binas'},
        {property: 'og:site_name', content: 'John Louie Binas'},
        {property: 'og:type', content: 'website'},
        {property: 'og:image', content:  this.logo},
        {property: 'og:description', content: this.desc}
      ]
    }
  },
}
</script>

